import { pop, includes, insert } from "./helpers";

export const initialState = {
  houses: [],
  users: [],
  session: undefined,
  notifications: []
};

export function reducer(state, action) {
  const { notifications } = state
  switch (action.type) {
    // HOUSE REDUCERS
    case 'FETCH_HOUSES':{
      const equatable = (a, b) => a.slug === b.slug;
      return { ...state, houses: insert(state.houses, action.payload, equatable) };
    }
    // NOTIFICATIONS REDUCERS...
    case 'PUSH_NOTIFICATION':{
      const notification = action.payload
      const equatable = (a, b) => a.message === b.message;
      if (!includes(notifications, notification, equatable)){
        return { ...state, notifications: [...notifications, notification]}
      }
      break;
    }

    case 'POP_NOTIFICATION':{
      const notification = action.payload
      return { ...state, notifications: pop(notifications, notification)}
    }

    // SESSION REDUCERS...
    case "LOGIN":{
      return {...state, session: true, notifications: [...notifications, {message: "Login successfully"}]}
    }

    case "LOGOUT":{
      return {...state, session: undefined, notifications: [...notifications, {message: "Logout successfully"}]}
    }

    case 'FETCH_USERS':{
      const equatable = (a, b) => a.email === b.email;
      return { ...state, users: insert(state.users, action.payload, equatable) };
    }
    // END REDUCERS
    default: break;
  }
  return state
}