import React, { useContext, useEffect, Suspense } from 'react'
import { Store, fetchHouses } from '../../store';
import { Loader, AuthenticateUser } from "../components";

const HousesList = React.lazy(() => import('./_List'));

export default function List() {
  const { state, dispatch } = useContext(Store);
  
  const { houses } = state

  useEffect(() => {
    houses.length === 0 && fetchHouses(dispatch);
  });

  const props = {
    houses,
  }

  return (<>
    <AuthenticateUser />
    <Suspense fallback={<Loader message="Descargando el componente para usted..." absolute/>}>
      <HousesList {...props}/>
    </Suspense>
  </>)
}
