import axios from 'axios'

export const fetchHouses = (dispatch, page=0) => {
  let token = "Bearer " + localStorage.getItem("casascuba-jwt");
  
  axios({method: 'get', url: '/api/houses', headers: {'Authorization': token}})
  .then(response => { 
    return dispatch({
      type: "FETCH_HOUSES",
      payload: response.data
    })
  })
}
