import React, { useContext } from 'react'
import { Link } from "react-router-dom";

import { Paper, Grid, TextField, Button } from '@material-ui/core';

import { Store, login } from '../../store';
import { RedirectBack } from '../components';
import Centered from '../components/Centered';
import { makeStyles } from '@material-ui/styles';

import clsx from 'clsx';

const useStyles = makeStyles(theme => ({
  paper: {
    padding: 30
  },
  textField: {
    marginLeft: theme.spacing(1),
    marginRight: theme.spacing(1),
  },
  dense: {
    marginTop: theme.spacing(2),
  },
  submitButton:{
    margin: 10
  },
})) 

const RequestLogin = dispatch => {
  const email = document.getElementById('login-email').value
  const password = document.getElementById('login-password').value

  login(email, password, dispatch)
}

export default function Login(props) {
  let params = new URLSearchParams(props.location.search);
  const back_url = params.get("back")

  const { dispatch } = useContext(Store)
  const session = localStorage.getItem('casascuba-jwt')
  
  const classes = useStyles();

  return (
    <Centered absolute centerContent>
      {session && <RedirectBack to={back_url}/>}
      <Paper className={classes.paper}>
        <Grid>
          <Grid item>
            <TextField
              id="login-email"
              label="Email"
              className={clsx(classes.textField, classes.dense)}
              margin="dense"
              variant="outlined"
            />
          </Grid>
          <Grid item>
            <TextField
              id="login-password"
              label="Password"
              className={clsx(classes.textField, classes.dense)}
              margin="dense"
              variant="outlined"
              type="password"
            />
          </Grid>
          <Grid item>
            <Button 
              onClick={() => RequestLogin(dispatch)} 
              color="primary" 
              variant="contained"
              className={classes.submitButton}
            >
              Login
            </Button>
          </Grid>
          <Grid item>
            <Link to={{ pathname: "/users/new", search: "?back=" + back_url }}>Sing in instead</Link>
          </Grid>
        </Grid>
      </Paper>
    </Centered>
  )
}
