import { get, post } from 'axios'

export function login(email, password, dispatch) {
  const request = { auth: { email, password } }

  post('/api/user_token', request)
  .then(response => {
    localStorage.setItem("casascuba-jwt", response.data.jwt);
    return dispatch({
      type: "PUSH_NOTIFICATION",
      payload: {
        message: "Login successfully"
      }
    })
  })
  .catch(error => {
    // fix displayed error
    return dispatch({
      type: "PUSH_NOTIFICATION",
      payload: error
    })
  });
}

export function logout() {
  localStorage.removeItem("casascuba-jwt");
  return {
    type: "PUSH_NOTIFICATION",
    payload: {
      message: "Logout successfully"
    }
  }
}

export const fetchUsers = (dispatch, page=0) => {
  get('/api/users')
  .then(response => { 
    return dispatch({
      type: "FETCH_USERS",
      payload: response.data
    })
  })
}