export function pop(collection, element) {
  return collection.filter((value, _, __) => {
    return value !== element 
  });
}

export function includes(collection, element, equals) {
  for (const elem of collection) {
    if (equals(elem, element)){
      return true
    }
  }
  return false
}

export function insert(collection, elements, equals){
  const result = collection
  for (const element of elements) {
    if (!includes(result, element, equals)){
      result.push(element)
    }
  }
  return result;
}