import React from 'react';
import { AppBar, Slide, useScrollTrigger, Toolbar, Typography, Button, makeStyles } from '@material-ui/core';
import { withRouter } from "react-router-dom";

import { Link } from 'react-router-dom';

function HideOnScroll(props) {
  const { children, window } = props;
  // Note that you normally won't need to set the window ref as useScrollTrigger
  // will default to window.
  // This is only being set here because the demo is in an iframe.
  const trigger = useScrollTrigger({ target: window ? window() : undefined });
  
  return (
    <Slide appear={false} direction="down" in={!trigger}>
    {children}
    </Slide>
  );
}

const useStyles = makeStyles({
  title: {
    flexGrow: 1,
    textDecoration: 'none',
    color: 'inherit'

  },
})

function NavLink(props) {

  return (
    <Link {...props}>
      <Button>
        <Typography color="inherit" variant="caption" className="nav-link">{props.children}</Typography>
      </Button>
    </Link>
  )
}

function Navigation({location}) {
  const classes = useStyles()

  const session = localStorage.getItem('casascuba-jwt')

  const SessionButton = () => session?
    <NavLink to={{ pathname: "/logout", search: "?back=" + location.pathname }}>Salir</NavLink>:
    <NavLink to={{ pathname: "/login", search: "?back=" + location.pathname }}>Entrar</NavLink>;

  return (
    <HideOnScroll>
      <AppBar color="primary">
      <Toolbar>
        <Link to='/' className={classes.title}>
          <Typography variant="h6">
            CasasCuba®
          </Typography>
        </Link>
        <SessionButton/>
      </Toolbar>
      </AppBar>
    </HideOnScroll>
  )
}

export default withRouter(props => <Navigation {...props}/>)