import React, { useContext, useEffect, Suspense } from 'react'
import { Store, fetchUsers } from '../../store';
import Loader from "../components/Loader";

const UserList = React.lazy(() => import('./_List'));

export default function List() {
  const { state, dispatch } = useContext(Store);
  
  const { users } = state

  useEffect(() => {
    users.length === 0 && fetchUsers(dispatch);
  });

  const props = {
    users,
  }

  return (
    <Suspense fallback={<Loader message="Descargando el componente para usted..." absolute/>}>
      <UserList {...props}/>
    </Suspense>
  )
}
