import React from 'react'
import { fetchHouses } from "./action_house";
import { popNotification, pushNotification } from "./action_notifications";
import { login, logout, fetchUsers } from "./action_session";

import { reducer, initialState } from './reducer';

export const Store = React.createContext();

export function StoreProvider(props) {
  const [state, dispatch] = React.useReducer(reducer, initialState);
  const value = { state, dispatch };
  return (
    <Store.Provider value={value}>
      {props.children}
    </Store.Provider>
  )
}

export {
  // HOUSES
  fetchHouses,

  // NOTIFICATIONS
  popNotification,
  pushNotification,

  // SESSION
  login, 
  logout,
  fetchUsers
}