import React, { useContext } from 'react'

import { Snackbar, Slide, IconButton } from "@material-ui/core";
import CloseIcon from '@material-ui/icons/Close';

import { makeStyles } from "@material-ui/core/styles";

import { Store, popNotification } from '../../store';

const useStyles = makeStyles({
  snack: {
    top: 90
  }
})

export default function Notifications() {
  const { state, dispatch } = useContext(Store)
  const { notifications } = state
  const classes = useStyles();

  const handleClose = notification => {
    dispatch(popNotification(notification))
  }

  const transition = props => {
    return <Slide {...props} direction="left" />;
  }

  return (
    <div>
      {notifications.map(n => <div key={n.message}>
        <Snackbar
          open
          anchorOrigin={{vertical: "top", horizontal: "right"}}
          TransitionComponent={transition}
          ContentProps={{ 'aria-describedby': 'message-id', }}
          className={classes.snack}
          message={
            <span id="client-snackbar">
              {n.message}
            </span>
          }
          action={[
            <IconButton key="close" aria-label="close" color="inherit" onClick={() => handleClose(n)}>
              <CloseIcon/>
            </IconButton>,
          ]}
        />
      </div>)}
    </div>
  )
}
