import React, { Suspense, useContext } from 'react'

import Loader from '../components/Loader';
import { Store } from '../../store';
import { RedirectBack } from '../components';
import Centered from '../components/Centered';

const UserForm = React.lazy(() => import('./_Form'))

export default function Create(props) {
  let params = new URLSearchParams(props.location.search);
  const back_url = params.get("back")
  
  const {state, dispatch} = useContext(Store);

  return <>
    {state.session && <RedirectBack to={back_url}/>}
    <Suspense fallback={<Loader message="Descragando formulario..." absolute/>}>
      <Centered absolute centerContent>
        <UserForm trigger={dispatch}/>
      </Centered>
    </Suspense>
  </>
}
