import React from 'react'
import { makeStyles } from '@material-ui/styles';
import { Grid } from '@material-ui/core';
import clsx from 'clsx';

const useStyles = makeStyles({
  full: {
    width: '100%',
    height: '100%',
    boxSizing: 'inherit',
  },
  absolute:{
    position: 'absolute',
    top: 0,
    left: 0,
    display: 'table',
    touchAction: 'none',
    // Disable scroll and other actions..
  },
  realtive: {
  },
  centerd: {
    display: 'table-cell',
    verticalAlign: 'middle',
  },
  textCenter: {
    textAlign: 'center',
  }
})

export default function Centered({children, absolute, centerContent, background}) {
  const classes = useStyles()
  
  let container = classes.full
  container = clsx(container, absolute? classes.absolute: classes.realtive)
  container = centerContent? clsx(container, classes.textCenter): container

  return (
    <div className={container} style={{background: background? background: 'transparent'}}> 
      <div className={classes.centerd}>
        <Grid container direction="row" justify="center" alignItems="center">
          <Grid item>
            {children}
          </Grid>
        </Grid>
      </div>
    </div>
  )
}
