import React from 'react';
import './styles/App.css';
import { CssBaseline } from '@material-ui/core';
import { ThemeProvider } from '@material-ui/styles';
import {BrowserRouter as Router, Route, Switch} from 'react-router-dom'

import { defaultTheme } from './themes';
import { Navigation, Notifications } from './views/components';


import {Home, About} from './views';
import { HouseList, HouseNew, HouseShow, HouseEdit } from './views/house';
import { UserList, UserNew, UserShow, UserEdit, Login, Logout } from './views/user';

function Main() {
  return (
    <Switch>
      <Route exact path='/' component={Home}/>
      <Route exact path='/about' component={About}/>

      <Route exact path='/houses' component={HouseList}/>
      <Route exact path='/houses/new' component={HouseNew}/>
      <Route exact path='/houses/:id' component={HouseShow}/>
      <Route exact path='/houses/:id/edit' component={HouseEdit}/>

      <Route path='/login' component={Login}/>
      <Route path='/logout' component={Logout}/>

      <Route exact path='/users' component={UserList}/>
      <Route exact path='/users/new' component={UserNew}/>
      <Route exact path='/users/:id' component={UserShow}/>
      <Route exact path='/users/:id/edit' component={UserEdit}/>
    </Switch>
  )
}

function App() {
  return (
    // Set Dark theme into store
    <ThemeProvider theme={defaultTheme}>
      <CssBaseline/>
      <Router>
        <Navigation />
        <div className="container">
          <Notifications />
          <Main />
        </div>
      </Router>
    </ThemeProvider>
  );
}

export default App;
