import React, { Fragment, useContext } from 'react'
import { Redirect } from "react-router-dom";
import { Store, pushNotification } from '../../store';
import { withRouter } from "react-router-dom";

function AuthenticateUser({location}) {
  const user = localStorage.getItem('casascuba-jwt')
  const { dispatch } = useContext(Store)

  if (!user){
    dispatch(pushNotification({
      message: "You need to login in order to access this resource :("
    }))
  }

  return user? <Fragment />: <Redirect to={{pathname: "/login", search: "?back=" + location.pathname}} />
}

export default withRouter(props => <AuthenticateUser {...props} />)